import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import { update } from '../../storePriority/redux/action';

@connect(
  state => ({...state.rawConveProduce, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '单号',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode',
      // render: text=><a href="#">{text}</a>
      render: (text, record, index) => <a onClick = { () => {
        const {history, history:{location}} = this.props
        window.open(`${location.pathname}/${record.orderCode}`)
        // history.push(`${location.pathname}/${record.orderCode}`)
      }}>{text}</a>
    },{
      title: '货主名称',
      width: 150,
      dataIndex: 'shipperName',
      key: 'shipperName'
    }, {
      title: '仓库编码',
      width: 150,
      dataIndex: 'warehouseCode',
      key: 'warehouseCode'
    }, {
      title: '生产工单',
      width: 150,
      dataIndex: 'productionOrder',
      key: 'productionOrder'
    } , 
    // {
    //   title: '流水号',
    //   width: 200,
    //   dataIndex: 'serialNumber',
    //   key: 'serialNumber'
    // },
     {
      title: '状态',
      width: 150,
      dataIndex: 'statusDesc',
      key: 'statusDesc'
    }, {
      title: '创建人',
      width: 150,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '创建时间',
      width: 200,
      dataIndex: 'createTime',
      key: 'createTime'
    }]
  }

    //一些操作
    onEdit = (modalType, record) => {
      const {update, modalId} = this.props
      update({
        [modalType]: true, 
        modalId: [record.id], 
        orderCode: record.orderCode, 
        shipperName: record.shipperName,
        requisitionsTypeName: record.requisitionsTypeName,
        requisitionsTotalAmount: record.requisitionsTotalAmount,
        remark: record.remark   
      })
    }

  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value ) return
    // console.log(...searchParams)
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if ( newProps.userLoation.value != userLoation.value ) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange =  (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, isAsc: true, ...searchParams, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, isAsc: false, ...searchParams, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, ...searchParams, size: pagination.pageSize}})
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



