import * as actionType from './actionType'
import { getData } from 'util'
import { API_MERCHANTPRIORITYCHOICE_PAGE, API_CREATEMERCHANTPRIORITYCHOICE_CREATE, API_DELETEMERCHANTPRIORITYCHOICE_DELETE, API_UPDATESORTNUMBYUPSIDEDOWN_CREATE, API_SHOPINFO_PAGE, API_SHOPINFOEXPORT_EXPORT, API_DELETESHOPINFO_DELETE } from 'api'
//同步
export const update = (object) => ({
  type: actionType.STOREPRIORITY_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({ type: actionType.STOREPRIORITY_UPDATE, data: { tableLoading: true } })
  getData({ api: API_MERCHANTPRIORITYCHOICE_PAGE, ...reqData }).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STOREPRIORITY_UPDATE,
      data: {
        total: data.result.length,
        tableLoading: false,
        searchResult: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STOREPRIORITY_FILE
    })
  })
}


//获取弹窗列表
export const handleGetTableList = (reqData) => (dispatch) => {
  dispatch({ type: actionType.STOREPRIORITY_UPDATE, data: { popTableLoading: true } })
  let reqObj = reqData.searchResult
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
      console.log(i)
    }
  }
  getData({ api: API_SHOPINFO_PAGE, ...reqData }).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STOREPRIORITY_UPDATE,
      data: {
        popTableLoading: false,
        popSearchResult: data.result || [],
        dataResultLen: data.result.length
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STOREPRIORITY_FILE
    })
  })
}

// 创建分配规则
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({ type: actionType.STOREPRIORITY_UPDATE, data: { modalTuttonLoading: true } })
  let api = API_CREATEMERCHANTPRIORITYCHOICE_CREATE
  getData({ api, ...reqData }).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STOREPRIORITY_UPDATE,
      data: {
        modalTuttonLoading: false,
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOREPRIORITY_FILE
    })
  })
}

// 删除快递商
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({ api: API_DELETEMERCHANTPRIORITYCHOICE_DELETE, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOREPRIORITY_FILE
    })
  })
}

