//判断上移还是下移
export const handleUpOrDown = (reqData, callBack) => (dispatch) => {
  let api = API_UPDATESORTNUMBYUPSIDEDOWN_CREATE
  getData({ api, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOREPRIORITY_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  getData({ api: API_SHOPINFOEXPORT_EXPORT, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOREPRIORITY_FILE
    })
  })
}

//删除
export const handleTableDel = (reqData, callBack) => (dispatch) => {
  getData({ api: API_DELETESHOPINFO_DELETE, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOREPRIORITY_FILE
    })
  })
}



